const MenuItemsList = [
    {
        title: '',
        path: '/login',
        is_loggedIn: false
    },
    {
        title: '',
        path: '/profile',
        is_loggedIn: true
    },
    {
        title: '',
        rootPath: '/calculator',
        items: [
            {
                title: '',
                path: '/calculator/general'
            },
            {
                title: ' ()',
                path: '/calculator/training'
            }
        ],
        is_loggedIn: true,
        has_dropdown: true
    }
];

export default MenuItemsList;



